'use strict';
import { MEASUREHOME} from '@/config/image.js';
import { APPTMEASUREHOME, QUERYPROGRESS} from '@/config/router.js';
var _self;
export default {
	data() {
		return{
			img:MEASUREHOME,
			show:false
		}
	},
	methods:{
		imgshow(){
			_self.show=true
		},
		queryProgressBtn(){
			
			uni.navigateTo({
				url:QUERYPROGRESS
			})
			
		},
		apptMeasureHomeBtn(){
			uni.navigateTo({
				url:APPTMEASUREHOME
			})
		}
		
			
	},
	async onLoad() {
		_self=this
	}
};
